<template>
  <div id="Container">
    <kinesis-container>
        <kinesis-element :strength="20">
            <h1 v-on:click="revolution = false; count++" v-if="revolution">
                Revolution
            </h1>

            <h1 v-on:click="revolution = true; count++" v-if="!revolution">
                Re Volution
            </h1>

            <h1 style="top: 30%; left: 40%;" v-on:click="revolution1 = false; count++" v-if="count > 2 && revolution1">
                Revolution
            </h1>
            <h1 style="top: 30%; left: 40%;" v-on:click="revolution1 = true; count++" v-if="count > 2 && !revolution1">
                Re Volution
            </h1>
            <h1 style="top: 40%; left: 10%;" v-on:click="revolution2 = false; count++" v-if="count > 5 && revolution2">
                Revolution
            </h1>
            <h1 style="top: 10%; left: 10%;" v-on:click="revolution2 = true; count++" v-if="count > 5 && !revolution2">
                Re Volution
            </h1>
            <h1 style="top: 80%; left: 90%;" v-on:click="revolution3 = false; count++" v-if="count > 35 && revolution3">
                Revolution
            </h1>
            <h1 style="top: 85%; left: 90%;" v-on:click="revolution3 = true; count++" v-if="count > 35 && !revolution3">
                Re Volution
            </h1>
            <curve-text v-if="count > 3 && count < 10">Revolution</curve-text>
            <curve-text v-if="count > 4 && count < 7 " id="curved" style="position: absolute;top: 80%; left: 40%;">Revolution</curve-text>
            <curve-text v-if="count > 7" id="curved" style="position: absolute;top: 80%; left: 40%;">Re Volution</curve-text>
            <curve-text v-if="count > 7 && count < 10" id="curved" style="position: absolute;top: 80%; left: 40%;">Re Volution</curve-text>
            <curve-text v-if="count > 10 && count < 15" id="curved" style="position: absolute;top: 80%; left: 40%;">Re Volution</curve-text>
            <curve-text v-if="count > 5" style="top: 50%; left: 40%;">Revolution</curve-text>
            <curve-text v-if="count > 6 && count <8" style="top: 50px; left: 40%;">Revolution</curve-text>
            <curve-text v-if="count > 7" style="position: absolute;top: 50px; left: 40%;">Revolution</curve-text>
            <curve-text v-if="count > 12 && count < 15" id="curved" style="position: absolute;top: 80%; left: 10%;">ReVolution</curve-text>
            <curve-text v-if="count > 15" id="curved" style="position: absolute;top: 80%; left: 10%;">Re Volution</curve-text>
            <curve-text v-if="count > 20 && count < 30" id="curved" style="position: absolute;top: 25%; left:80%;">ReVolution</curve-text>
            <curve-text v-if=" count > 30" id="curved" style="position: absolute;top: 25%; left: 80%;">Re Volution</curve-text>
        </kinesis-element>
        <h3>
            Revolution Count: {{globalCount}}
        </h3>
    </kinesis-container>
  </div>
</template>

<script>
import { KinesisContainer, KinesisElement} from 'vue-kinesis';
import CurveText from '@inotom/vue-curve-text';
export default {
    name: 'Revolution',
    components:{
        KinesisContainer,
        KinesisElement,
        CurveText
    },

    data: function(){
        return{
            revolution:true,
            revolution1:true,
            revolution2:true,
            revolution3:true,
            count: 0,
            globalCount:0,
            limit:1        
        }
    },
    watch: {
        count: function () {
            if( this.count > this.limit){
                this.count = 0; 
                this.limit += Math.random()*5;
                this.globalCount--;
            }
            this.globalCount++;
        } 
  }

}
</script>

<style>
div{
    margin: auto;
    height: 90%;
    
}

#Container{
    box-sizing: border-box;
    border: 50px black solid;
}
h1{
    position: absolute;
    top: 50%;
    left: 50%;
    margin: auto;
    translate: (-50%, -50%);
}

#curved{
    position: absolute;
    font-size: 30px;
}

</style>